import React, { Component } from 'react';
import styled from 'styled-components'
import Container from './theme/grid/Container'
import './App.css';


//component permettant d'afficher un court texte sur le fonctionnement de l'app
class Description extends Component {
  render() {

    return (

       
      <Container>
				<div class="container">
					 <div class="row">
					    	<div class="col-sm-8">
					    	</div>
					   		 <div class="col-sm-12">
								<p>This web interface was designed to help you creating a list of medias you love. It can be series, movies, etc. For each presented medias, hit the like or dislike button. Another media will be then displayed</p>
							</div>
							<div class="col-lg-5">
							</div>
					</div>
				</div>

		</Container>

      );
  }

}


export default Description;



