import React, { Component } from 'react';
import styled from 'styled-components';
import './App.css';
import Container from './theme/grid/Container'




class ChoixLikesDislikes extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loopActive: false,
      shuffleActive: false,
    };

  }



  ajoutListeLikes() {

  	//cration d'une nouvelle array pour les mdias liks par l'utilisateur
    let likes = new Array();

    //s'il y a dj des lments dans le localStorage correspondant  des mdias liks, on les rcupre dans une liste
    if (localStorage.getItem("likes")) {
      let likes1 = JSON.parse(localStorage.getItem("likes")) || [];
      likes1.forEach(function(element) {

        likes.push(element);



      })
    }
    //on envoie le mdias dans la liste des mdias liks
    likes.push(this.props.listedemedias[0]);
    //on retire le mdias de la liste de mdias 
    this.props.listedemedias.splice(0, 1);
    //cre dans le localStorage une liste de film liks
    localStorage.setItem("likes", JSON.stringify(likes));
    this.render();
    this.forceUpdate()
  }

  dislikeMedia(){

  	//le mdias dislik est retir de la liste afin de ne pas tre propos une seconde fois  l'utilisateur 
  	this.props.listedemedias.splice(0, 1);
  	this.render();
    this.forceUpdate();

  }

  render() {
    const listedemedias = this.props.listedemedias;
    const firstMedia = (listedemedias && listedemedias[0]) ? listedemedias[0] : undefined;

    return (
      <Container>
			<div className="container">
        { firstMedia ? (
            <div class="card mb-4 box-shadow">
                <div class="card-header">
                  <h4 class="my-0 font-weight-normal">{firstMedia.original_title}</h4>
                </div>
                <div class="card-body">
                  <h1 class="card-title pricing-card-title">{firstMedia.original_title}</h1>
                  <ul class="list-unstyled mt-3 mb-4">
                    
                    <li><h3>Tagline:</h3>{firstMedia.tagline}</li>
                    <li><h3>Synopsis:</h3>{firstMedia.overview}</li>
                    <li><h3>Genre:</h3> {firstMedia.genre[0].name}</li>
                    <li><h3>Release:</h3>{firstMedia.genre[0].release}</li>
                    
                    

                    
                    
                  </ul>
                  
                    
                    
              <button type="button" class="btn btn-success btn-lg btn-block" onClick={this.ajoutListeLikes.bind(this)}>Like</button>
              <button type="button" class="btn btn-danger btn-lg btn-block" onClick={this.dislikeMedia.bind(this)}>Dislike</button>
              
              </div>
              </div>
          ): "" 
        }
		</div>
		</Container>
      );
  }

}

export default ChoixLikesDislikes;


